.screen-reader-text {
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.logo {
  height: 4rem;
  width: 4rem;
  --tw-border-opacity: 1;
  border-width: 2px;
  border-color: rgb(229 231 235 / var(--tw-border-opacity) );
  border-radius: 9999px;
}

.entry-content > :not(:last-child) {
  margin-bottom: 1rem;
}

.entry-content a {
  text-decoration-line: underline;
}

.entry-content a:hover {
  --tw-text-opacity: 1;
  color: rgb(239 68 68 / var(--tw-text-opacity) );
}

.entry-content h2, .entry-content h3 {
  margin-top: 2.5rem;
  font-weight: 700;
  line-height: 1.25;
}

.entry-content h2 {
  font-size: 1.5rem;
  line-height: 2rem;
}

.entry-content h3 {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.entry-content :not(pre) > code {
  --tw-bg-opacity: 1;
  background-color: rgb(243 244 246 / var(--tw-bg-opacity) );
  padding: .25rem;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;
  font-size: .875rem;
  line-height: 1.25rem;
}

.entry-content ol {
  list-style-type: decimal;
  list-style-position: inside;
}

.entry-content ul {
  list-style-type: disc;
  list-style-position: inside;
}

.entry-content li {
  padding-left: 2rem;
}

.entry-content blockquote {
  --tw-border-opacity: 1;
  border-left-width: 4px;
  border-color: rgb(199 210 254 / var(--tw-border-opacity) );
  --tw-bg-opacity: 1;
  background-color: rgb(224 231 255 / var(--tw-bg-opacity) );
  margin-left: 0;
  margin-right: 0;
  padding: 2rem;
}

.entry-content blockquote cite {
  margin-top: 1rem;
  display: block;
}

.navigation {
  justify-content: center;
  margin-bottom: 3rem;
  display: flex;
}

.navigation ul {
  list-style-type: none;
  display: flex;
}

.navigation li {
  margin-left: .5rem;
  margin-right: .5rem;
}

.navigation .current {
  font-weight: 700;
}

